import React from 'react';
import Nav from '../header/NavBar';
import Footer from '../footer/Footer';

const Layout =(props)=>(
    <div>
    <header><Nav/></header>
    <main>
    {props.children}
    </main>
    <footer><Footer/></footer>
    </div>
    
);
export default Layout;


