import React,{Component} from 'react';
import {Collapse,Nav} from 'reactstrap'
import NavToggler from './NavbarToggler';
class NavItems extends Component{

  state={
    isNavOpen:false,
  }
  toggleNav=()=>{
    this.setState({
      isNavOpen:!this.state.isNavOpen
    })
  }
  render(){
    return(
    <div className="toolbar">
       <nav className="">
<div className='navforlarge'>
       <ul className="navigationItems me-auto mb-2 mb-lg-0">
          <li className="nav-item">
            <a className="nav-link" href="/">Product</a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="/">Resources</a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="/">Inspiration</a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="/">Pricing</a>
          </li>
          </ul>

</div>


       <div className='navforsmall'>
<NavToggler onClick={this.toggleNav} />
<Collapse isOpen={this.state.isNavOpen} navbar>
                          <Nav navbar>
                          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
          <li className="nav-item">
            <a className="nav-link" href="/">Product</a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="/">Resources</a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="/">Inspiration</a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="/">Pricing</a>
          </li>
          </ul>
                          </Nav> 
                          
                      </Collapse> 

</div>
</nav>
<div className='navbar-brand' >
   <a href='/'><span className='fa fa-meh-o fa-2x'></span> <h1>mailchimp</h1> </a>

</div>
 
<div className='nav-search'>
  <div className='search-btn'>
      <span className='fa fa-search fa-2x'></span>
  </div>
  <div className='nav-btn login'><a href='/'>Login</a></div>
  <div className='nav-btn signup'><a href='/'>Sign Up Free</a></div>

  </div>

    </div>
   

    );


          }}
export default NavItems;


